/* Universial Tag */

*{
    box-sizing: border-box;
}

body{
    min-height: 100vh;
    overflow: hidden;
    background-repeat: no-repeat;
    margin: 0;
    background: 
    linear-gradient(217deg, rgba(216, 243, 241, 0.8), rgba(255,0,0,0) 70.71%),
    linear-gradient(127deg, rgba(255, 170, 199, 0.8), rgba(0,255,0,0) 70.71%),
    linear-gradient(336deg, rgba(255, 231, 205, 0.8), rgba(0,0,255,0) 70.71%) 
    ;
}
/* Typography */

ul,li{
    list-style: none;
    margin: 0;
    padding: 0;
}





/* Container */
 
#container{
    padding: 10px 20px;
    margin: 100px auto;  
    width: 320px;
    height: 520px;
    background: linear-gradient(to bottom, transparent, rgba(255, 226, 236, 0.8));
    border-radius: 15px;
    border: 1px solid white;
    box-shadow: rgba(119, 119, 119, 0.187) 0px 48px 100px 0px;
}

/* Title */

#title{
    text-align: center;
    font-family: 'Fredoka One', cursive;
    letter-spacing: 5px;
    font-size: 12px;
    color: rgb(0, 50, 97);

}

/* Main */

#main{
    
    display: inline-block;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.4);
    color: rgb(97, 97, 97);
    width: 100%;
    height:70%;
    overflow-y: auto;
    scrollbar-width: none;
    font-family: 'Mulish', sans-serif;
}

.item{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.trashButton{
    text-align: right;
    background-color: transparent;
    border: transparent;
    cursor: pointer;
    color: rgb(118, 118, 118);
}
.trashButton:hover{
    transform: scale(1.2);
    transition: all 250ms ease-in-out;
    color: rgb(137, 210, 186);
    
}


/* input */

input{
    outline: none;
    border: 1px solid transparent;
    width: 100%;
    height: 30px;
    border-radius: 2px;
}


/* Bottom */

#bottom{
    width: 100%;
    height: 50px;
    text-align: center;
    padding: 13px 0;
}

button.plusButton{
    background-color: transparent;
    border: transparent;
    font-size: 40px;
    color: rgb(0, 50, 97);
    cursor: pointer; 

}

button.plusButton:hover{
    transform: scale(1.1);
    transition: all 250ms ease-in-out;
}
